.portfolio {
    max-width: 100%;
    height: 900px;
    background-image: url('../../img/Rectangle\ 6.png');
    background-size: 100% 900px;
    .portfolio_wrapper{
        position: relative;
        max-width: 100%;
        height: 850px;
    }
}

.container_portfolio{
    position: absolute;
    margin: 205px 345px;
    width: 1170px;
    height: 690px;
    display: flex;
    color: #FFFFFF;
    .images{
       flex-flow: nowrap;
       height: 580px;
       margin-top: 100px;
       div:nth-child(1){
        margin-left: 290px;
        z-index: 2;
        
       }
       div:nth-child(2){
         z-index: 1;
        
        }
    }
}

.content {
    display: flex;
    position: relative;
    flex-direction: column;
    width: 670px;
    height: 560px;
    .portfolio_title{
        @include bold;
        width: 450px;
        height: 50px;
        font-size: 60px;
        line-height: 40px;
        margin-left: 345px;
        display: flex;
        white-space: nowrap;
        justify-content: space-between;
        img{
            height: 4px;
            align-self: center;
        }

        
    }
    .tort_name {
        @include medium;
        font-size: 40px;
        width: 700px;
        margin-top: 100px;
        text-align: center;
    }
    .description{
        @include light;
        width: 650px;
        font-size: 30px;
        margin: 40px 55px;
        text-align: center;
        justify-self: center;
    }
}

.selection{
    width: 215;
    height: 30px;
    display: flex;
    margin: 205px 275px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    img:nth-child(2){
        max-width: 10px;
        max-height: 10px;
    }
    img:nth-child(6){
        max-width: 10px;
        max-height: 10px;
    }
    img:nth-child(3){
        max-width: 15px;
        max-height: 15px;
    }
    img:nth-child(5){
        max-width: 15px;
        max-height: 15px;
    }
    img:nth-child(4){
        max-width: 20px;
        max-height: 20px;
    }
}

